<template>
	<div id="deposit-order-list">
		<public-header title="押金订单"></public-header>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="content">
			<van-tabs v-model="filter.items.status" @change="onToggleStatus" sticky offset-top="46">
				<van-tab :title="item.text" v-for="(item, index) in orderStatusArr" :name="item.status" :key="index">
					<van-list v-model="orders.loading" :finished="orders.finished" @load="onLoad" :offset="100" :immediate-check="true">
						<template v-if="orders.data.length > 0">
							<template v-for="(item, index) in orders.data">
								<div class="order-item">
									<div class="info" @click="util.jsUrl('/mall/pages/deposit/detail', {id: item.id})">
										<div class="flex-lr">
											<div>
												<span>支付时间</span>
												<span>{{item.paytime_cn}}</span>
											</div>
											<div>{{item.status_cn.text}}</div>
										</div>
										<div class="order-item-info font-14">
											<div class="flex-lr margin-10-tb">
												<span>押金费</span>
												<span class="c-danger">{{Lang.dollarSign}}{{item.final_fee}}</span>
											</div>
											<div class="flex-lr margin-10-tb">
												<span>押金类型</span>
												<span>{{item.category.title}}</span>
											</div>
										</div>
									</div>
									<div class="btn-group van-hairline--top">
										<template v-if="item.status == 1">
											<van-button @click="onChangeStatus(item.id, 'refund_apply')" plain round hairline size="normal"  type="danger" >申请退押金</van-button>
										</template>
										<van-button @click="util.jsUrl('/mall/pages/deposit/detail', {id: item.id})" plain round hairline type="default">详情</van-button>
									</div>
								</div>
							</template>
							<load type="loaded" text="我是有底线的" bgcolor="#f5f5f5" v-if="orders.finished"></load>
						</template>
						<div class="common-no-con" v-else>
							<img src= "static/img/order_no_con.png" alt="" />
							<p>您还没有押金订单！</p>
						</div>
					</van-list>
				</van-tab>
			</van-tabs>
		</div>
	</div>
</template>

<script>
	import PublicFooter from '@/components/footer'
	import PublicHeader from '@/components/header'
	import load from '@/components/load'
	import { Dialog } from 'vant';
	export default {
		components: {
			PublicHeader,
			PublicFooter,
			load,
			Dialog
		},
		data() {
			return {
				orderStatusArr: [
					{
						text: '退押金中',
						status: 2
					},
					{
						text: '已支付',
						status: 1
					},
				],
				orders: {
					page: 1,
					psize: 10,
					finished: false,
					loading: false,
					data:[]
				},
				filter: {
					items: {
						status: 0
					}
				},
				showPreLoading: true,
				menufooter: {},
			}
		},
		methods: {
			onToggleStatus(status) {
				this.filter.items.status = status;
				this.onLoad(true)
			},
			onChangeStatus(id, type) {
				var params = {
					url: 'wmall/mall/deposit/order_update',
					data: {
						id: id,
						type: type,
					}
				};
				this.util.jspost(params);
			},
			onLoad(force) {
				if(force) {
					this.orders = {
						page: 1,
						psize: 10,
						loading: true,
						finished: false,
						data:[]
					};
				}
				if(this.orders.finished) {
					return false;
				}
				this.util.request({
					url: 'wmall/mall/deposit/order_list',
					data: {
						page: this.orders.page,
						psize: this.orders.psize,
						status: this.filter.items.status,
						menufooter: 1,
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.hideLoading();
					result = result.message;
					this.orders.data = [...this.orders.data, ...result.orders];
					this.orders.loading = false;
					if(result.orders.length < this.orders.psize) {
						this.orders.finished = true;
					}
					this.orders.page++;
					this.menufooter = window.menufooter;
				})
			},
			hideLoading(){
				this.showPreLoading = false;
			},
		},
		mounted() {
		}
	}
</script>

<style>
	#deposit-order-list .order-item{
		margin: 10px;
		border-radius: 5px;
		background-color: #fff;
		font-size: 14px;
	}
	#deposit-order-list .order-item .info{
		padding: 15px;
	}
	#deposit-order-list .order-item .btn-group{
		display: flex;
		flex-flow: row nowrap;
		justify-content: flex-end;
		white-space: nowrap;
		overflow-x: scroll;
		padding: 10px 15px;
	}
	#deposit-order-list .order-item .btn-group .van-button{
		margin-left: 10px;
		height: 40px;
	}
	#deposit-order-list .common-no-con{
		position: fixed;
		z-index: 5;
		top: 32%;
		left: 0;
		width: 100%;
		height: 200px;
		text-align: center;
		color: #768491;
		font-size: 16px;
	}
	#deposit-order-list .common-no-con img{
		max-width: 25%;
		vertical-align: middle;
	}
	#deposit-order-list .common-no-con p{
		margin: 20px 0;
	}
	#deposit-order-list .common-no-con .common-no-con-a{
		display: inline-block;
		height: 35px;
		line-height: 35px;
		color: #fff;
		font-size: 14px;
		background: #ff2d4b;
		padding: 0 10px;
		border-radius: 35px;
	}
</style>


